.header {
	background: url(../images/header/header-bg.jpg) no-repeat;
	background-size: cover;
	font-family: 'Lato', sans-serif;
	color: $premier-text-color;
	//height: 100vh;

	&__top {
		@include row-flex();
		//justify-content: space-between;
		align-items: center;
		padding-top: 15px;
	}
}

.logo {
	@include col();
	@include size(1);
	@include shift-left(1);
	font-size: 35px;
	font-weight: 700;
	text-transform: uppercase;
}

/*.block-menu {
	@include col();
	@include size(5);
	display: flex;
	justify-content: space-between;
	align-items: center;
}*/

.menu__link,
.submenu__link {
	color: $premier-text-color;
	padding: 5px 20px;
	font-weight: 700;

	&:hover {
		font-family: 'Montserrat', sans-serif;
		font-weight: 700;
		border-bottom: 3px solid #ffff00;
	}
}

.menu__link_active {
	border-bottom: 3px solid #ffff00;
}

.menu {
	@include col();
	@include size(3);
	@include shift-left(9);
	font-size: 36px;
	&__items {
		display: flex;
	}
}

.menu__items li {
	position: relative;

}

.submenu {
	position:absolute;
	top: 50px;
  right: 0;
	display: none;
	z-index: 1;
}

.menu__items li:hover .submenu {
	display:block;
}

.button-menu {
	@include col();
	@include size(1);
	@include shift-left(1);
	text-align: center;
	cursor: pointer;
}

.content-wrap {
	@include row-flex();
}

.social {
	@include col();
	@include size(1);
	margin-top: 235px;
	text-align: center;
	cursor: pointer;
}
	
.title {
	@include col();
	@include size(12);
	@include shift(1);
font-size: 510px;
font-weight: 700;
text-align: center;
letter-spacing: 1.22px;

span {
	display: block;
	font-size: 110px;
	font-weight: 400;
}
}
.circle-nav-content {
	@include col();
	@include size(1);
	flex-direction: column;
	justify-content: center;

	a {
		margin: 10px 0;
	}
}

.circle {
	display: block;
	background: $premier-text-color;
	margin: 0 5px;

	&-nav {
		display: flex;
		align-items: center;
		margin-top: 40px;

	}

	&-last {
		width: 13px;
		height: 14px;
		border-radius: 100%
	}

	&-small {
		width: 16px;
		height: 16px;
		border-radius: 100%
	}

	&-middle {
		width: 20px;
		height: 20px;
		border-radius: 100%
	}

	&-big {
		width: 25px;
		height: 24px;
		border-radius: 100%
	}

	&-active {
		background: #ffff00;
	}
}

.button-next {
	cursor: pointer;
	img {
		width: 116px;
		display: block;
	}
}

.button-view {
	padding: 0 170px;
	height: 100px;
	border: none;
	border-radius: 10px;
	border: 7px solid #f5e700;
	background: transparent;
	color: #f5e700;
	font-size: 55px;
	font-weight: 400;
	letter-spacing: 1.1px;
	cursor: pointer;
	
	

	&:hover {
		background: #f5e700;
		color: #000000;
	}
}

.header__bottom {
	margin-top: 231px;
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
}

.head__text {
	font-size: 36px;
font-weight: 400;
text-transform: uppercase;
letter-spacing: 0.72px;
text-align: center;
}